import React from 'react';
import { withIntl } from '../i18n';
import PageWrapper from '../components/PageWrapper';
import Paragraphs from '../components/Paragraphs';
import { ContentTitle, Details, Links, PageTitle } from '../ui/utilities';

const Contact = ({ t, ...props }) => (
  <PageWrapper title={t('contact.title')} reader {...props}>
    <PageTitle>{t('contact.title')}</PageTitle>
    <ContentTitle className="t-h1" aria-hidden="true">{t('contact.title')}</ContentTitle>
    <h2>{t('contact.name')}</h2>
    <p>
      <a href={t('contact.addressLink')} target='_blank'>
        {t('contact.address')}
      </a>
    </p>
    <Details>
      <Links>
        <li>
          {t('contact.phoneTitle')}:{' '}
          <a href={`tel:${t('contact.phoneNumber')}`}>{t('contact.phone')}</a>
        </li>
        <li>
          {t('contact.faxTitle')}: <span>{t('contact.fax')}</span>
        </li>
        <li>
          {t('contact.emailTitle')}:{' '}
          <a href={`mailto:${t('contact.email')}`}>{t('contact.email')}</a>
        </li>
      </Links>
    </Details>
    <h3>{t('contact.schedule.title')}</h3>
    <Paragraphs lines={t('contact.schedule.calendar')} />
  </PageWrapper>
);

export default withIntl()(Contact);
